


















import Vue from "vue";
import {Component, Prop} from "vue-property-decorator"
import {ChannelItems} from "@/services/VideoTypings";
import {YoutubeVideoAPI} from "@/services/YoutubeVideoAPI";
import Store from "@/store";

@Component({
  name: "Teams"
})
export default class Teams extends Vue {
  data() {
    return {
      channels: []
    }
  }
  private channels?: Array<ChannelItems>;
  async mounted(): Promise<void> {
    for (const channelId of Store.state.channels) {
      const result = new YoutubeVideoAPI(channelId);
      const data = await result.getChannel();
      this.channels!.push(data.items[0])
    }
  }

  private getChannelUrl(id: string) {
    return `https://youtube.com/${id}`
  }
}
